import ContactList from "../../components/Contacts/ContactList";
import { useLocation } from "react-router-dom";
import { utilService } from "../../services/util.service";
import { useState } from "react";

import { ReactComponent as ExitIcon } from "../../assets/icon/exit.svg"
import SelectModal from "../../components/SelectModal/SelectModal";


const ContactsPage = () => {
    const location = useLocation();
    const [selectedFilters, setSelectedFilters] = useState({})
    // const [sort, setSort] = useState('newest');
    const cat = location.pathname.split("/")[2];

    const handleFilterDelete = (type) => {
        const filters = { ...selectedFilters }
        for (const filter in filters) {
            if (filters[filter] === type) {
                delete filters[filter]
            }
        }
        setSelectedFilters(filters)
    }

    return (
        <div className="body-wrapper space-pb--120 contact-category">
            <div className="breadcrumb-area space-pb--25">
                <SelectModal
                    selectedFilters={selectedFilters}
                    setSelectedFilters={setSelectedFilters}
                    cat={cat} />

                {Object.values(selectedFilters).length > 0 && <div className="container flex justify-space mt-4 mb-4">
                    <div className="flex align-center gap-2">
                        <ExitIcon />
                        <h4>Filters {`(${Object.values(selectedFilters).length})`}</h4>
                    </div>

                    <div className="flex align-center gap-2 contact-category-filters">
                        {Object.values(selectedFilters).map(filter => (
                            <h4 key={filter} className="filter__item">
                                {filter}
                                <span onClick={() => handleFilterDelete(filter)}><ExitIcon /></span>
                            </h4>
                        ))}
                    </div>
                </div>}
            </div>
            <ContactList cat={cat} filters={selectedFilters} title={utilService.getFirstLetterUppercase(cat)} />
        </div>
    )
}

export default ContactsPage